import React, { useEffect, useState } from "react";
import MouseTooltip from "./util/MouseTooltip";
import writeGood from "write-good";
import { Issue } from "./Issue";
import { syllable } from "syllable";
import { WORD_BAN_LIST } from "./BannedWords";
function AnnotatedText({ text }) {
    const WhitespaceRegex = /\s/g;
    const [mouseIndex, setMouseIndex] = useState(-1);
    const [suggestions, setSuggestions] = useState([]);
    const [issueCount, setIssueCount] = useState([]);
    useEffect(() => {
        const suggestions = writeGood(text);
        function addSuggestion(index, offset, reason) {
            suggestions.push({
                index,
                offset,
                reason,
            });
        }
        const paragraphRegex = /\n[^\n]+|[^\n]+\n|([^\n]+$)/gi;
        let match;
        while ((match = paragraphRegex.exec(text))) {
            const paragraph = match[0];
            const paragraphStart = match.index;
            // suggestions.push({
            //   index: match.index,
            //   offset: match[0].length,
            //   reason: "this is a paragraph",
            // });
            const sentenceRegex = /[^.]+./gi;
            while ((match = sentenceRegex.exec(paragraph))) {
                const sentence = match[0];
                const sentenceStart = match.index + paragraphStart;
                const sentenceOffset = sentence.length;
                // console.log(sentence, sentence.split(" ").length);
                const wordCount = sentence.split(" ").length;
                if (wordCount > 20) {
                    // prettier-ignore
                    addSuggestion(sentenceStart, sentenceOffset, `Long sentence! Words = ${wordCount}, Expected <= 20`);
                }
                if (wordCount > 25) {
                    // prettier-ignore
                    addSuggestion(sentenceStart, sentenceOffset, `Very long sentence! Words = ${wordCount}, Expected <= 25`);
                }
                const wordRegex = / [^ ]+|[^ ]+ /gi;
                while ((match = wordRegex.exec(sentence))) {
                    const word = match[0].trim();
                    const wordStart = match.index + sentenceStart;
                    const wordOffset = match[0].length;
                    const syllables = syllable(word);
                    if (syllables > 2) {
                        // prettier-ignore
                        addSuggestion(wordStart, wordOffset, `Long word! Syllables = ${syllables}, Expected <= 2`);
                    }
                    if (syllables > 3) {
                        // prettier-ignore
                        addSuggestion(wordStart, wordOffset, `Very long word! Syllables = ${syllables}, Expected <= 3`);
                    }
                    if (WORD_BAN_LIST[word]) {
                        // prettier-ignore
                        addSuggestion(wordStart, wordOffset, `"${word}": ${WORD_BAN_LIST[word]}`);
                    }
                }
            }
        }
        const issueCount = [...new Array(text.length)].map(() => 0);
        for (const { index, offset } of suggestions) {
            for (let i = 0; i < offset; ++i) {
                issueCount[index + i] += 1;
            }
        }
        setSuggestions(suggestions);
        setIssueCount(issueCount);
    }, [text]);
    function issueCountToColor(issues) {
        if (issues >= 5)
            return "text-red-500";
        else {
            return [
                "",
                "text-yellow-500",
                "text-yellow-800",
                "text-orange-500",
                "text-orange-800",
            ][issues];
        }
    }
    return (React.createElement("div", { className: 'flex flex-row w-full max-w-md flex-wrap font-mono' },
        text.split("").map((c, index) => (React.createElement("div", { key: index, className: `${WhitespaceRegex.test(c) ? "px-2" : ""} ${issueCountToColor(issueCount[index])} ${index === mouseIndex ? "bg-slate-500" : ""}`, onMouseOver: () => setMouseIndex(index) }, c))),
        React.createElement(MouseTooltip, { offsetY: 10 },
            React.createElement("div", { className: 'bg-white p-2 border border-black rounded-md text-black max-w-sm' }, suggestions.map(({ reason, index, offset }, suggestionIndex) => mouseIndex >= index &&
                mouseIndex <= index + offset && (React.createElement(Issue, { label: '!', description: reason, key: suggestionIndex })))))));
}
export default AnnotatedText;
